
<template>
  <Details>
      <div  id='write'  class = 'is-node'><h1><a name='header-n0' class='md-header-anchor '></a>Git使用基本流程</h1><p><strong>编写文件=&gt;把文件提交到暂存区=&gt;提交到本地仓库=&gt;提交到Git服务器仓库</strong></p><h3><a name='header-n4' class='md-header-anchor '></a>1.创建文件夹，切换到创建的文件夹内，并初始化（把当前文件夹变成本地仓库）</h3><p>​	命令：<strong>git init</strong></p><ul><li><p>注意：如果没有配置绑定用户名及邮箱，这里要配置用户名及邮箱，实现在本机一次配置多次使用加上--global</p><p>命令：<strong>git config --global user.name xxx</strong> </p><p>命令：<strong>git config --global user.email <a href='mailto:xxx@qq.com' target='_blank' class='url'>xxx@qq.com</a></strong> </p></li><li><p>移除全局用户名</p><p>命令：<strong>git config --global --unset user.name</strong></p></li><li><p>下载Git仓库文件</p><p>命令： <strong>git clone 仓库地址 本地需要装仓库文件的文件名</strong></p></li></ul><h3><a name='header-n25' class='md-header-anchor '></a>2.查看本地仓库文件状态，Untracked表示未追踪，该文件夹中有新创建的文件(可跳过此步骤)</h3><ul><li><p>显示结果中，对于仓库根目录中的文件直接显示</p><p>命令：<strong>git status</strong></p></li></ul><h3><a name='header-n32' class='md-header-anchor '></a>3.把所有文件提交到暂存区中</h3><ul><li><p>对于已经进入到暂存区的文件，文件的状态是：changes to be committed</p></li><li><p>对于已经进入过暂存区的文件，再次修改文件，文件的状态是：modified</p></li><li><p>将指定目录下面的文件放到暂存区</p><p>命令：<strong>git add 完整文件名(文件名且带有后缀)</strong></p></li><li><p>将当前仓库中的所有的没有被提交到暂存区的代码放到暂存区</p><p>命令：<strong>git add .</strong></p></li><li><p>默认为将修改的文件和新添加的文件添加到git系统的暂存区（注意：不包括删除的）</p><p>命令：<strong>git add *</strong></p><p>命令：<strong>git add *.js</strong>	将修改的和新添加的以.js为后缀的文件添加到暂存区</p></li><li><p>将(修改和删除的文件)的操作提交暂存区，但是这个不会将新添加的文件提交到暂存区</p><p>命令：<strong>git add -u</strong>或者<strong>git add --update</strong></p></li><li><p>将所有的修改与删除和未跟踪的文件都告诉暂存区(新创建的文件，修改的文件，删除的文件）</p><p>命令：<strong>git add -A</strong>或者<strong>git add --all</strong></p></li><li><p>可以链式操作,提交所有文件</p><p>命令：<strong>git add -u+git add *</strong></p></li></ul><h3><a name='header-n72' class='md-header-anchor '></a>4.提交到本地仓库</h3><p>​	命令：<strong>git commit -m 备注说明</strong></p><ul><li>此时必须要进行备注才能提交成功</li><li>或者输入git commit 后再输入i或者a键进入编辑模式，再备注说明，完成后退出编辑模式：esc键，然后保存并退出就输入:wq</li></ul><h3><a name='header-n82' class='md-header-anchor '></a>5.提交到Git服务器仓库</h3><ul><li><p>Git服务器仓库是空仓库，可使用push提交</p><p>命令：<strong>git push 服务器仓库地址 master</strong></p></li><li><p>Git服务器仓库不是空仓库，就是之前已经提交过，现在要继续提交更新，可使用pull提交</p><p>命令：<strong>git pull 服务器仓库地址 master</strong></p></li><li><p>因为每次提交文件都需要仓库服务器地址，可用变量保存服务器地址，每次使用的时候输入变量名替换服务器仓库地址就可以了</p><p>命令：<strong>git remote add urls 服务器地址</strong></p></li><li><p>删除服务器地址：<strong>git remote remove 变量名</strong></p></li><li><p>变量重命名：<strong>git remote rename 旧变量名 新变量名</strong></p></li><li><p>获取变量名保存的服务器地址：<strong>git remote get-url 变量名</strong></p></li><li><p>重新设置变量名保存的服务器地址：<strong>git remote set-url 变量名 服务器地址</strong></p><p><br/></p></li></ul><h3><a name='header-n113' class='md-header-anchor '></a>隔离文件=&gt;不希望把某个文件或者文件夹上传到Git服务器仓库</h3><ul><li><p>在.git同级目录，也就是根目录，创建一个文件.gitignore</p><p>1.手动创建文件.gitignore (电脑会有提示，必须要键入文件名)</p><p>2.使用bash命令：<strong>touch .gitignore</strong> (推荐使用这种方法)</p></li><li><p>打开.gitignore文件进行编辑，一行一行添加不需要git管理的文件</p><p>比如Vue里面的node_modules文件就不需要提交的。</p><p>示例：node_modules/*	这是把node_modules所有文件隔离</p><p>也可设置单独的文件，node_modules/*.js	这是把node_modules文件夹下的所有js文件隔离</p></li><li><p><strong>注意也需要把.gitignore文件隔离</strong></p><p>在文件里面加上<strong>.gitignore</strong>就行</p></li></ul><h3><a name='header-n136' class='md-header-anchor '></a>删除本地文件及Git服务器上的文件</h3><h3><a name='header-n137' class='md-header-anchor '></a>第一种方式：</h3><pre class="md-fences md-end-block" lang=""> <div class="CodeMirror cm-s-inner CodeMirror-wrap"><div style="overflow: hidden; position: relative; width: 3px; height: 0px; top: 4px; left: 4px;"></div><div class="CodeMirror-scrollbar-filler" cm-not-content="true"></div><div class="CodeMirror-gutter-filler" cm-not-content="true"></div><div class="CodeMirror-scroll" tabindex="-1"><div class="CodeMirror-sizer" style="margin-left: 0px; margin-bottom: 0px; border-right-width: 30px; padding-right: 0px; padding-bottom: 0px;"><div style="position: relative; top: 0px;"><div class="CodeMirror-lines" role="presentation"><div role="presentation" style="position: relative; outline: none;"><div class="CodeMirror-measure"></div><div class="CodeMirror-measure"></div><div style="position: relative; z-index: 1;"></div><div class="CodeMirror-code" role="presentation"><div class="CodeMirror-activeline" style="position: relative;"><div class="CodeMirror-activeline-background CodeMirror-linebackground"></div><div class="CodeMirror-gutter-background CodeMirror-activeline-gutter" style="left: 0px; width: 0px;"></div><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  右击删除/rm 文件名 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; --&gt;物理删除</span></pre></div><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git add 文件名 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; --&gt;把删除操作提交暂存区</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git commit -m 备注说明 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;  --&gt;把删除操作提交本地主仓库</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git push 仓库地址 master &nbsp; &nbsp; &nbsp; &nbsp; --&gt;把删除操作提交服务器仓库</span></pre></div></div></div></div></div><div style="position: absolute; height: 30px; width: 1px; border-bottom: 0px solid transparent; top: 112px;"></div><div class="CodeMirror-gutters" style="display: none; height: 142px;"></div></div></div></pre><h3><a name='header-n139' class='md-header-anchor '></a>第二种方式：</h3><pre class="md-fences md-end-block" lang=""> <div class="CodeMirror cm-s-inner CodeMirror-wrap"><div style="overflow: hidden; position: relative; width: 3px; height: 0px; top: 4px; left: 4px;"></div><div class="CodeMirror-scrollbar-filler" cm-not-content="true"></div><div class="CodeMirror-gutter-filler" cm-not-content="true"></div><div class="CodeMirror-scroll" tabindex="-1"><div class="CodeMirror-sizer" style="margin-left: 0px; margin-bottom: 0px; border-right-width: 30px; padding-right: 0px; padding-bottom: 0px;"><div style="position: relative; top: 0px;"><div class="CodeMirror-lines" role="presentation"><div role="presentation" style="position: relative; outline: none;"><div class="CodeMirror-measure"><pre><span>xxxxxxxxxx</span></pre></div><div class="CodeMirror-measure"></div><div style="position: relative; z-index: 1;"></div><div class="CodeMirror-code" role="presentation"><div class="CodeMirror-activeline" style="position: relative;"><div class="CodeMirror-activeline-background CodeMirror-linebackground"></div><div class="CodeMirror-gutter-background CodeMirror-activeline-gutter" style="left: 0px; width: 0px;"></div><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git rm 文件名 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; --&gt;删除文件</span></pre></div><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git add 文件名<span class="cm-tab" role="presentation" cm-text="	"> </span><span class="cm-tab" role="presentation" cm-text="	">    </span><span class="cm-tab" role="presentation" cm-text="	">    </span><span class="cm-tab" role="presentation" cm-text="	">    </span><span class="cm-tab" role="presentation" cm-text="	">    </span> --提交删除操作到暂存区</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git commit -m 备注说明 &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;  --&gt;把该删除操作提交本地主仓库</span></pre><pre class=" CodeMirror-line " role="presentation"><span role="presentation" style="padding-right: 0.1px;"> &nbsp;  git push 仓库地址 master &nbsp; &nbsp; &nbsp; &nbsp; --&gt;把该删除操作提交服务器仓库</span></pre></div></div></div></div></div><div style="position: absolute; height: 30px; width: 1px; border-bottom: 0px solid transparent; top: 112px;"></div><div class="CodeMirror-gutters" style="display: none; height: 142px;"></div></div></div></pre><ul><li><p>强制删除文件</p><p>命令：<strong>git rm 文件名 -f</strong></p></li></ul><h3><a name='header-n147' class='md-header-anchor '></a>版本回滚（恢复某个版本的所有文件）</h3><ul><li><p>查看日志--完整的</p><p>命令：<strong>git log</strong></p></li><li><p>查看日志--精简版信息</p><p>命令：<strong>git log --oneline</strong></p></li><li><p>版本回滚操作</p><p>命令：<strong>git reset --hard 版本号</strong></p></li><li><p>在版本回滚后，使用git log获取不到之前的操作记录，可使用以下命令进行查看所有历史记录再进行版本回滚</p><p>历史记录包括：每一次提交的信息；以及每一次版本回滚的操作；分支切换操作</p><p>命令：<strong>git reflog</strong></p></li><li><p>版本回滚后提交到Git服务器仓库（此命令是强制更新服务器上的仓库）</p><p>命令：<strong>git push -f 服务器地址 master</strong></p></li></ul><h3><a name='header-n176' class='md-header-anchor '></a>误删操作</h3><ul><li><p>可使用恢复命令进行恢复刚刚删除的文件</p><p>命令：<strong>git checkout --文件名</strong></p></li></ul><h3><a name='header-n183' class='md-header-anchor '></a>把暂存区的文件撤回来</h3><p>​	命令：<strong>git reset HEAD --文件名</strong></p><h3><a name='header-n186' class='md-header-anchor '></a>使用分支</h3><ul><li><p>查看当前拥有的所有分支（带*前缀的是当前分支）</p><p>命令：<strong>git branch</strong></p></li><li><p>创建新分支</p><p>命令：<strong>git branch 分支名</strong></p></li><li><p>切换到某个分支上</p><p>命令：<strong>git checkout 分支名</strong></p></li><li><p>合并分支（注意合并分支的前必须要切换到主分支上）</p><p>命令：<strong>git merge 分支名 -m 备注说明</strong></p></li><li><p>合并完分支后，不使用这个分支了，就必须要把这个分支给删掉</p><p>命令：<strong>git branch -d 分支名</strong></p></li></ul><p>&nbsp;</p><p>&nbsp;</p><h2><a name='header-n217' class='md-header-anchor '></a>在GitHub创建个人网站</h2><ul><li>只能显示静态文件，比如HTML、css、js、md等。</li><li>新建一个仓库，仓库名字固定格式：<strong>github用户名.github.io</strong></li><li>输入该仓库地址就可以看到这些文件了(默认显示index.html)</li><li>如果有README.md 也会自动加载README.md</li><li>其他文件不会自动渲染，需要确定的文件名</li><li>访问该网站必须使用https协议访问</li></ul></div>
  </Details>
</template>
<script>
export default {
  name:'git_use',
}
</script>


